.row-height {
    height: 100%;
    position: fixed;
    background-color: #212529;
    overflow-x: hidden;
    border-top: whitesmoke;
    z-index: 1;
    width: 20%;
  }

.row-chat {
    transform: translate(20%);
    z-index: 0;
    color:black;
    background-color: white;
    height: 100%;
    width: 80%;
    position: fixed;
}

.btnb {
    margin-right: 10%;
    margin-left: 10%;
}

.btn {
    width: 100%;
}

.chantoggle {
  margin-top: 12px;
}

.chantoggle input {
  margin-bottom: 12px;
}

hr {
    background-color: #6c757d !important;
    opacity: 1 !important;
    margin: 1rem 0 0 0 !important;
}

hr:not([size]) {
    height: 2px !important;
}

#subTitle {
    font-size: small !important;
    color: #6c757d !important;
}

.userinfo {
    color: white;
    padding: 0px 0px 0px 0px !important;
}

.userinfo a {
  color: white;
  text-decoration: none;
  margin-left: 12px;
}

.userinfo a:hover {
  color: rgb(170, 170, 170);
}

.chanlist a {
  text-decoration: none;
}

.chaninfo {
    color: white;
    padding-left: 30px;
}

.chanlist ul li:hover {
    background-color: #6c757d;
    color:white !important;
}

.userinfo p {
    margin: -6px 0px 0px 0px;
    font-size: 12px;
    color: #8e99a1;
}

.friendlist ul {
    padding-left: 0px;
}

.friendlist ul li {
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 10px;
}

.friendlist ul li:hover {
    background-color: #6c757d;
    color:white !important;
}

.avatar {
    padding: 3px 0 0 0;
}

.status{
    width:8px;
    height:8px;
    border-radius:50%;
    display:inline-block;
    margin-right:7px;
  }
  .green{
    background-color:#58b666;
  }
  .orange{
    background-color:#ff725d;
  }
  .blue{
    background-color:#6fbced;
  }

.chatheader {
    border-bottom: solid 3px #6c757d;
    height: 6%;
}

.chatheader p {
    transform: translate(3%, 38%);
    font-size: 20px;
}

.chatbox {
    border-bottom: solid 3px #6c757d;
    height: 74%;
}

.chatfooter {
    border-bottom: solid 3px #6c757d;
    height: 14.5%;
}

#chat{
    padding-left:0;
    margin:0;
    list-style-type:none;
    overflow-y:scroll;
    height:100%;
    border-top:2px solid #fff;
    border-bottom:2px solid #fff;
  }
  #chat li{
    padding:10px 30px;
  }
  #chat h2,#chat h3{
    display:inline-block;
    font-size:13px;
    font-weight:normal;
  }
  #chat h3{
    color:#bbb;
  }
  #chat .entete{
    margin-bottom:5px;
  }
  #chat .message{
    padding:20px;
    color:#fff;
    line-height:25px;
    max-width:90%;
    display:inline-block;
    text-align:left;
    border-radius:5px;
  }
  #chat .me{
    text-align:right;
  }
  #chat .you .message{
    background-color:#6c757d;
  }
  #chat .me .message{
    background-color:#212529;
  }